import React, { useState } from "react";
import passwordValidator from "password-validator";

export default function Text({
  id,
  label,
  maxLength,
  value,
  required,
  onChange,
  sendError,
  type,
  ...props
}) {
  const [error, setError] = useState("");
  const schema = new passwordValidator();
  id === "pin"
    ? schema
        .is()
        .min(5)
        .is()
        .max(5)
        .has()
        .not()
        .letters()
    : schema.is().min(6);

  const handleChange = ev => {
    ev.persist();
    if (type === "password" && props.register) {
      let errors = schema.validate(ev.target.value, { list: true });
      if (id === "pin")
        errors = errors.concat(
          ev.target.value[0] === "0" ? ["Your pin may not start with a 0"] : []
        );
      if (errors.length) {
        setError(
          "Your " +
            (id === "pin" ? "pin" : "password") +
            " doesn't meet the following: " +
            errors
              .map(
                error =>
                  "Your " +
                  (id === "pin" ? "pin" : "password") +
                  " needs to " +
                  (error === "min"
                    ? "contain at least " +
                      ((id === "pin" ? 5 : 6) + " characters")
                    : error === "uppercase"
                    ? "include at least one uppercase character"
                    : error === "lowercase"
                    ? "include at least one lowercase character"
                    : error === "digits"
                    ? "include at least one digit"
                    : error === "max"
                    ? "contain less than 20 characters"
                    : error === "letters"
                    ? "contain numbers only"
                    : error)
              )
              .join(", ")
        );
        sendError && sendError(true);
      } else {
        setError("");
        sendError && sendError(false);
        onChange({ [ev.target.id]: ev.target.value });
      }

      console.log(errors);
    } else onChange(ev);
  };
  return (
    <div>
      <input
        type={(id === "pin" ? "pin" : type) || "text"}
        name={id}
        defaultValue={value}
        onChange={handleChange}
        placeholder={label}
        id={id}
        className="form-control"
        maxLength={maxLength}
      />
      {(props.error || error || props.pageError) && (
        <div
          style={{
            color: "red",
            fontStyle: "italic"
          }}
        >
          {"Error: " +
            (props.error || "") +
            " " +
            (props.pageError || "") +
            " " +
            error}
        </div>
      )}
    </div>
  );
}
